.navbr{
    position: fixed;
    top: 0;
    width: 100%;
    padding-top: 40px;
    padding-bottom: 40px;
    height: 30px;
    z-index: 100;
    display: flex;
    justify-content: space-evenly;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}
a:-webkit-any-link {
    text-decoration: none;
}


.nav__black {
    background-color: #fff;
    padding-top: 15px;
    padding-bottom: 15px;
}

.nav__logo {
    display: flex;
    align-items: center;
    margin-right: 25%;
    width: 115px;
    object-fit: contain;
    color: white;
}

.nav__logoBlack {
    width: 115px;
    object-fit: contain;
    color: black;
}


.hamburger {
    background-color: transparent;
    border: 0;
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    
}

.hamburger:focus {
    outline: none;
}

.hamburger_black {
    color: black;
    
}

.nav__components {
    display: flex;
    align-items: center;
    object-fit: contain;

    font-family: sofia-pro, system-ui, BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    font-weight: 500;
    color: rgb(96, 99, 123);
    font-size: 0.875rem;
    
    margin-bottom: 0.25rem;
    
}

.menu {
    margin-right: 0.5rem;
}


.list_components {
    display: flex;
    flex-direction: column;
    height: 75vh;
    width: 100%;
    border-radius: 45px;
    background-color: rgb(24, 24, 28);
    position: absolute;
    top: 110px;
    z-index: 60;
}

.list_components__black{
    top: 60px;
}

.nav_link {
    font-family: sofia-pro, system-ui, BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    font-weight: 500;
    color: rgb(96, 99, 123);
    font-size: 0.875rem;
    margin-right: 0.5rem;
    margin-bottom: 0.25rem;
}

.nav_link_black {
    color: black;
}


.list_link {
    font-family: sofia-pro, system-ui, BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    font-weight: 700;
    letter-spacing: -0.05em;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    padding: 1rem;
    padding-left: 13%;
    font-size: 2.427rem;
    color: rgb(96, 99, 123);
    cursor: pointer;

}

.list_link:hover {
    background: rgba(125, 134, 212, 0.15);
}

.list_link_black {
    color: #fff;
}

.btn {
    border:1px;
    border-radius: 9999px;
    background-color: #fff;
    opacity:0.9;
    padding: 10px 50px 25px;
    font-size: 13px;
    /* font-family: "montserrat"; */
    cursor: pointer;
    /* margin: 10px; */
    transition: 0.6s;
    position: relative;
    overflow: hidden;
}

.btn1 {
    color: rgb(24, 24, 28);
}

.btn1:hover {
    color: #fff;
    /* z-index: 2; */
}

.btn::before {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 0%;
    background: #3498db;
    z-index: -1;
    transition: 0.6s;
}

.btn1::before {
    top: 0;
    border-radius: 0 0 50% 50%;
}

.btn1:hover::before {
    height: 180%;
}

  @media screen and (max-width: 1020px) {
      .btn {
          display: none;
      }
  }

