.brand {
    background-color: #fff;
    height:100vh;
    width: 100%;
    margin-top: 8rem;
    border-radius: 45px 45px 1px 1px;
}

.brand__text{
    margin-top: 5%;
    margin-right:10%;
    margin-left: 50%;
    padding-top: 10%;
}

.brand__heading{
    font-weight: 700;
    font-family: sofia-pro, system-ui, BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    color: rgb(51, 52, 62);
    font-size: 4.7rem;
    line-height: 0.809;
}

.brand__paragraph {
    font-family: sofia-pro-soft, system-ui, BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    font-weight: 400;
    color: rgb(96, 99, 123);
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    letter-spacing: 0.006em;
    font-size: 1.414rem;
    line-height: 1.618;
}

@media screen and (max-width: 1020px) {
    .brand__heading {
        font-size: 4rem;
        font-weight: 650;
    }
}

@media screen and (max-width: 820px) {
    .brand__heading {
        font-size: 3.5rem;
        font-weight: 600;
    }
}

@media screen and (max-width: 620px) {

    .brand {
        height: 80vh;
    }

    .brand__heading {
        font-size: 2.5rem;
        font-weight: 600;
    }

    .brand__paragraph {
        font-size: 1rem;
    }

    .brand__text{
        margin-top: 5%;
        margin-right:12%;
        margin-left: 40%;
        padding-top: 10%;
    }
}