.intro{
    margin-top: 16%;
    margin-left: 16%;
    margin-right: 40%;
    box-sizing: inherit;
}

.text1 {
    font-family: sofia-pro, system-ui, BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    line-height: 0.809;
    font-size: 4.61696rem;
    color: rgb(247, 248, 251);
    font-weight: 600;
    letter-spacing: -0.04em;
    margin-bottom: 0;
}

.text2 {
    font-family: sofia-pro, system-ui, BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    line-height: 0.809;
    font-size: 4.61696rem;
    color: rgb(247, 248, 251);
    font-weight: 600;
    opacity: 0.7;
    margin-top: 0;
    letter-spacing: -0.04em;
}

@media screen and (max-width: 1020px) {
    .text1,.text2 {
        font-size: 4rem;
        font-weight: 550;
    }
    .intro {
        margin-left: 23%;
        margin-right: 20%;
    }
}

@media screen and (max-width: 620px) {
    .text1,.text2 {
        font-size: 3rem;
        font-weight: 550;
    }
    .intro {
        margin-top: 30%;
        margin-left: 23%;
        margin-right: 20%;
    }
}

