.work{
    background-color: #fff;
    height:100vh;
    width: 100%;
    border-radius:1px 1px 45px 45px ;
}

.work_heading {
    width: 100%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    padding-left: 3rem;
    padding-right: 3rem;
    margin-left: auto;
    margin-right: auto;
    -webkit-box-align: center;
    align-items: center;
    box-sizing: border-box;
    max-width: 72rem;
}

h5 {
    font-weight: 600;
    font-family: sofia-pro, system-ui, BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    font-size: 1.84679rem;
    color: rgb(51, 52, 62);
    letter-spacing: -1.3px;
    margin: 0px;
}

hr {
    background-color: rgb(148, 150, 169);
    opacity: 0.5;
    margin-left: 3rem;
    height: 2px;
}

.featured_work {
    display: flex;
    margin: 5rem 5%;
    align-items: center;
    justify-content: center;
}

.zyppys {
    display: flex;
    flex-direction: column;

}

.zyppys_img {
    width: 60vh;
    height: 40vh;
    object-fit: cover;
    border-radius: 1.8rem;
    margin-right: 40px;
    transition: transform 450ms;
}

.zyppys_img:hover{
    transform: scale(1.08);
}

.zyppys_logo {
    height: 3rem;
    margin-right: 20.5rem;
    margin-top: 1rem;
}

.zyppys_para {
    font-family: sofia-pro-soft, system-ui, BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    font-weight: 400;
    color: rgb(96, 99, 123);
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    margin-left: 1.5rem;
    letter-spacing: 0.006em;
    font-size: 1.414rem;
    line-height: 1.618;
}

.hl {
    
    display: flex;
    flex-direction: column;
}

.phone_video {
    width: 60vh;
    height: 40vh;
    object-fit: cover;
    border-radius: 1.8rem;
    margin-left: 40px;
    transition: transform 450ms;
}

.phone_video:hover {
    transform: scale(1.08);
}

.hl_logo {
    height: 3rem;
    margin-right: 18.5rem;
    margin-top: 1rem;
}

.hl_para {
    font-family: sofia-pro-soft, system-ui, BlinkMacSystemFont, -apple-system, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    font-weight: 400;
    color: rgb(96, 99, 123);
    
    margin-left: 3.5rem;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
    letter-spacing: 0.006em;
    font-size: 1.414rem;
    line-height: 1.618;
}


@media screen and (max-width: 1020px) {
    .featured_work {
        flex-direction: column;
    }
    .work {
        
        padding-bottom: 22rem;
    }
    .hl {
        padding-top: 4rem;
    }
}

@media screen and (max-width: 620px) {
    .phone_video {
        margin-left: 10px;
        width: 50vh;
        height: 30vh;
    }

    .zyppys_img {
        margin-right: 0px;
        margin-left: 5px;
        width: 50vh;
        height: 30vh;
    }

    .zyppys_logo {
        height: 3rem;
        margin-right: 13.5rem;
        margin-top: 1rem;
    }

    .hl_logo {
        height: 2.5rem;
        margin-right: 15.5rem;
        margin-top: 1rem;
    }

    .hl_para {
        
        margin-left: 2.5rem;
    }

    .work {
        margin-top: 0px;
    }
}